<template>
  <div :class="{'type-box': true, 'border-99': isSelect}" ref="moveDiv" :style="{'transform': 'rotate(' + rotate + 'deg)', 'height': height + 'px', 'width': width + 'px'}" @mousedown="down" @touchstart="down" @mousemove="move" @touchmove="move" @mouseup="end"
    @touchend="end" @touchmove.prevent>
    <span v-if="isSelect" class="z-index" @click="controlLevel"></span>
    <span v-if="isSelect" class="del" @click="delDom"></span>
    <span v-if="isSelect" class="rotate" @mousedown="rotateDown" @touchstart="rotateDown" @mousemove="rotateMove" @touchmove="rotateMove" @mouseup="end" @touchmove.prevent></span>
    <span v-if="isSelect" class="expand" @mousedown="scaleDown" @touchstart="scaleDown" @mousemove="scaleMove" @touchmove="scaleMove" @mouseup="end" @touchmove.prevent></span>
    <slot name="content" />
  </div>
</template>

<script>
export default {
  name: 'pop',
  props: {
    isSelect: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      flags: false, // 
      position: {
        x: 0,
        y: 0
      },
      nx: '',
      ny: '',
      dx: '',
      dy: '',
      xPum: '',
      yPum: '',

      rotate: 0,

      isRotate: false, // 
      offsetX: '', // 
      offsetY: '', // 
      clientWidth: '', // 
      clientHeight: '', // 

      height: '', // 
      width: '', // 

      isScale: false, // 
      scaleX: 1,
      scaleY: 1,     // 
      
      timer: null

    }
  },
  methods: {
    // 
    controlLevel () {
      this.$emit('controlLevel', '')
    },
    // 
    close () {
      this.$emit('closeEdit', false)
    },
    // 
    down () {
      console.log('', this.timer)
      this.timer = null
      if (!this.isRotate && !this.isScale) {
        this.flags = true
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }

        this.position.x = touch.clientX
        this.position.y = touch.clientY
        this.dx = this.$refs.moveDiv.offsetLeft
        this.dy = this.$refs.moveDiv.offsetTop
      }
    },
    move () {
      if (this.flags) {
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }

        this.nx = touch.clientX - this.position.x
        this.ny = touch.clientY - this.position.y
        this.xPum = this.dx + this.nx
        this.yPum = this.dy + this.ny

        this.$refs.moveDiv.style.left = this.xPum + 'px'
        this.$refs.moveDiv.style.top = this.yPum + 'px'
        // 
        document.addEventListener('touchmove', function () { // 1.2  touchmove
          // event.preventDefault(); //jq 
          event.stopPropagation() // jq  stopPropagation()
        }, false)
      }
    },
    // 
    end () {
      this.flags = false
      this.isRotate = false
      this.isScale = false
      this.timer = setTimeout(function () {
        this.close()
        console.log('')
      }.bind(this), 2000)
    },

    // 
    rotateDown () {
      if (!this.flags && !this.isScale) {
        this.isRotate = true
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }
        console.log('222', touch, this.$refs.moveDiv.offsetLeft)
        this.offsetX = this.$refs.moveDiv.offsetLeft
        this.offsetY = this.$refs.moveDiv.offsetTop

        this.offsetX = this.$refs.moveDiv.offsetLeft
        this.offsetY = this.$refs.moveDiv.offsetTop
      }
    },
    rotateMove () {
      if (this.isRotate) {
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }

        var mouseX = touch.pageX // ,pageXclientY + body.scrollTop - body.clientTop,y+body-bodyborder-top,offsetX
        var mouseY = touch.pageY
        var ox = mouseX - this.offsetX - this.$refs.moveDiv.clientWidth / 2  // cx,cy
        var oy = mouseY - this.offsetY - this.$refs.moveDiv.clientWidth / 2
        var to = Math.abs(ox / oy)
        var angle = Math.atan(to) / (2 * Math.PI) * 360 // 

        if (ox < 0 && oy < 0) { // js
          angle = -angle
        } else if (ox < 0 && oy > 0) { // ,3
          angle -= 180
        } else if (ox > 0 && oy < 0) { // 1
          
        } else if (ox > 0 && oy > 0) { // 2
          angle = 180 - angle
        }
        this.rotate = angle + 141

        // 
        document.addEventListener('touchmove', function () { // 1.2  touchmove
          // event.preventDefault(); //jq 
          event.stopPropagation() // jq  stopPropagation()
        }, false)
      }
    },

    // 
    scaleDown () {
      if (!this.flags && !this.isRotate) {
        this.isScale = true
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }
        this.offsetX = touch.pageX
        this.offsetY = touch.pageY
        this.height = this.$refs.moveDiv.clientHeight
        this.width = this.$refs.moveDiv.clientWidth

        this.clientHeight = this.height
        this.clientWidth = this.width
      }
    },
    scaleMove () {
      if (this.isScale) {
        var touch
        if (event.touches) {
          touch = event.touches[0]
        } else {
          touch = event
        }
        console.log('333', touch.pageX, this.offsetX, touch.pageY)
        if(touch.pageX > this.offsetX) {
          this.width = touch.pageX / this.offsetX * this.clientWidth
        }
        if(touch.pageY > this.offsetY) {
          this.height = touch.pageY / this.offsetY * this.clientHeight
        }

        // 
        document.addEventListener('touchmove', function () { // 1.2  touchmove
          // event.preventDefault(); //jq 
          event.stopPropagation() // jq  stopPropagation()
        }, false)
      }
    },
    delDom () {
      console.log('')
      this.$emit('delDom', null)
    }
  }
}
</script>

<style lang="scss" scoped>
  .type-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-1.12rem);
    height: 2.25rem;
    width: 2.25rem;
    border: 1px solid #fff;
  }
  .border-99{
    border: 1px dashed #999;
  }

  .z-index,
  .del,
  .expand,
  .rotate {
    display: block;
    height: 0.4rem;
    width: 0.4rem;
    border-radius: 100%;
    border: 0.01rem solid #999;
    color: #131313;
    text-align: center;
    font-size: 0.36rem;
    line-height: 0.4rem;
  }

  .z-index {
    position: absolute;
    left: -0.4rem;
    top: -0.4rem;
  }

  .del {
    position: absolute;
    right: -0.4rem;
    top: -0.4rem;
  }

  .expand {
    position: absolute;
    bottom: -0.4rem;
    right: -0.4rem;
  }

  .rotate {
    position: absolute;
    left: -0.4rem;
    bottom: -0.4rem;
  }
</style>
